<template>
  <div class="th-back-top" v-show="flag" @click="clickHidden">
    <span class="bg-back2top2"></span>
  </div>
</template>
<script>
export default {
  name:"BackTop",
  data(){
    return{
      flag:true
    }
  },
  mounted(){
    window.addEventListener("scroll",()=>{
      let scrollTop = document.documentElement.scrollTop;
      if(scrollTop > 100){
        this.flag = true;
      }else{
        this.flag = false;
      }
    })
  },
  methods:{
    clickHidden(){
      document.querySelector(`#topbarid`).scrollIntoView({
        behavior: "smooth",   // 平滑过渡
        block:    "start"     // 上边框与视窗顶部平齐。默认值
      });
    }
  }
}
</script>
<style scoped>

.th-back-top{
  position:fixed;
  right:8vh;
  bottom:8vh;
  height: 6vh;
  width: 6vh;
  z-index:9;
  opacity:0.8;
  text-align:right;
}
.bg-back2top2 {
  display: inline-block;
  background: url('/static/button/backtop_02.png')
  no-repeat 0 0;
  -webkit-background-size: 6vh 6vh;
  background-size: 6vh 6vh;
  width: 6vh;
  height: 6vh;
}
.bg-back2top2:hover{
  display: inline-block;
  background: url('/static/button/backtop_01.png')
  no-repeat 0 0;
  -webkit-background-size: 6vh 6vh;
  background-size: 6vh 6vh;
  width: 6vh;
  height: 6vh;
}
</style>



